/**** Colors ****/
/**** Fonts ****/
/**** Images ****/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap");
@font-face {
  font-family: "Roboto", sans-serif;
  src: url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap");
}
/**** Global ****/
html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  font-family: "Roboto", sans-serif;
  font-size: 10px;
}

/**** Components ****/
.btn {
  padding: 20px;
  background: #ff4363;
  color: white;
  border: none;
  border-radius: 90px;
  font-size: 2.5rem;
  transition: 1s;
  width: fit-content;
  font-weight: 300;
  cursor: pointer;
}
.btn i {
  margin-right: 5px;
}
.btn:hover {
  background: #ff4363;
  color: white;
  transition: 1s;
}

.btn--white {
  background: white;
  color: #ff4363;
}
.btn--white:hover {
  background: #ff4363;
  color: white;
  transition: 1s;
}

.btn--transparent {
  background: transparent;
}

.hero {
  width: 100vw;
  min-height: 100vh;
  background-color: #ff4363;
  background-image: url("./img/heroImage.png");
  background-repeat: no-repeat;
  background-size: 70%;
  background-size: 60%;
  background-position: 115% 74%;
}
.hero__content {
  padding-top: 11%;
  padding-left: 13.5%;
  width: 40%;
  color: white;
}
.hero__title {
  font-size: 8.2rem;
  font-weight: 700;
  color: white;
  line-height: 1.3em;
  margin-bottom: 30px;
}
.hero__p {
  margin-top: 0;
  font-size: 2.1rem;
  line-height: 1.2em;
}
.hero__buttonsWrapper {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 70%;
}
.hero__buttonsWrapper .btn--transparent {
  margin-left: 30px;
  font-size: 2.1rem;
  font-weight: bold;
}

.advantages {
  display: flex;
  flex-wrap: wrap;
  place-content: center;
  place-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  margin-block: 10%;
}
.advantages .advantagesBox {
  text-align: center;
  width: 35%;
}

.header {
  display: none;
}